import { useEffect, useState } from "react";

import { motion } from "framer-motion";
import { styled } from "styled-components";

import Subtitle from "../../../components/SubTitle";
import { LineContainer } from "../../../components/Line";

const Web42 = () => {
  // line scroll
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    function handleScroll() {
      if (window.scrollY > 3300 && window.scrollY < 5000) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    }

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <>
      <ExpContainer>
        <div className="title-box">
          <p className="title-text up"> NEXT X  10</p>
        </div>
        <LineContainer>
          <div className="red"></div>
          {isScrolled ? (
            <motion.div
              initial={{ opacity: 1, x: 0 }} //  
              animate={{ opacity: 1, x: "10vw" }} //  
              exit={{ opacity: 0, x: "10vw" }}
              transition={{
                duration: 3,
              }}
              className="yellow"
            />
          ) : (
            <div className="yellow fixed"></div>
          )}
        </LineContainer>
        <p className="section-p-light up">
                  
                
        </p>
        <div className="info-box">
          <div className="info-col">
            <p className="section-p">period</p>
            <p className="section-p">position</p>
          </div>
          <div className="info-col">
            <p className="section-p-light">22.03 ~ 22.12</p>
            <p className="section-p-light"></p>
          </div>
        </div>
        <div className="flex-row">
          <div>
            <Subtitle content=" " />
            <li className="section-p-light line-gap ">
                    
            </li>
            <li className="section-p-light line-gap ">
                 - React
            </li>
            <div className="margin"></div>
          </div>
          <div>
            <Subtitle content="" />
            <li className="section-p-light line-gap ">  </li>
            <li className="section-p-light line-gap ">
                  
            </li>
            <li className="section-p-light line-gap "> </li>
          </div>
        </div>
      </ExpContainer>
    </>
  );
};

export default Web42;

const ExpContainer = styled.div`
  .up {
    z-index: 5;
  }
  .margin {
    height: 4rem;
  }
  .line-gap {
    padding-bottom: 1rem;
  }
  .flex-row {
    width: 70vw;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .title-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    /* padding-bottom: 3rem; */
    .title-text {
      color: #fff;
      font-size: 3rem;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }
  .info-box {
    display: flex;
    flex-direction: row;
    padding-top: 2rem;
    padding-bottom: 7rem;
    gap: 2rem;
    .info-col {
      display: flex;
      flex-direction: column;
      gap: 1rem;
    }
  }
`;
