import { useEffect, useState } from "react";

import { styled } from "styled-components";
import { motion } from "framer-motion";

import Subtitle from "../../../components/SubTitle";
import Button from "../../../components/Button";
import { LineContainer } from "../../../components/Line";

const Web41 = () => {
  const onClick = (num: number) => {
    num === 1 && window.open("https://yeonpick.kr/");
  };

  // line scroll
  const [isScrolled, setIsScrolled] = useState(false);

  useEffect(() => {
    function handleScroll() {
      if (window.scrollY > 1300 && window.scrollY < 3000) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    }

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <>
      <ExpContainer>
        <div className="title-box">
          <p className="title-text up"></p>
          <Button type={1} onClick={() => onClick(1)} />
        </div>
        <LineContainer>
          <div className="purple"></div>
          {isScrolled ? (
            <motion.div
              initial={{ opacity: 1, x: 0 }} //  
              animate={{ opacity: 1, x: "10vw" }} //  
              exit={{ opacity: 0, x: "10vw" }}
              transition={{
                duration: 3,
              }}
              className="pink"
            />
          ) : (
            <div className="pink fixed"></div>
          )}
        </LineContainer>
        <div className="info-container">
          <div>
            <p className="section-p-light content">
              3      
            </p>
            <div className="info-box">
              <div className="info-col">
                <p className="section-p">period</p>
                <p className="section-p">position</p>
                <p className="section-p">tech</p>
              </div>
              <div className="info-col">
                <p className="section-p-light">23.04 ~ 23.08</p>
                <p className="section-p-light">  </p>
                <p className="section-p-light">
                  Next.js, TypeScript, Redux, React Query, Emotion
                </p>
              </div>
            </div>
          </div>
          <motion.img
            src="/assets/png/img-yeonpick.png"
            alt="yeonpick"
            className="img up pointer"
            onClick={() => onClick(1)}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.9 }}
          />
        </div>
        <Subtitle content="  " />
        <li className="section-p-light line-gap ">
          React  Next.js   
        </li>
        <li className="section-p-light line-gap ">Redux, React Query </li>
        <li className="section-p-light line-gap ">
                   
        </li>
        <div className="margin"></div>
        <Subtitle content="  /" />
        <li className="section-p-light line-gap ">
                
        </li>
        <li className="section-p-light line-gap ">
               
        </li>
        <li className="section-p-light line-gap ">
            CTA     A/B Testing 
        </li>
        <li className="section-p-light line-gap ">
                
        </li>
        <div className="margin"></div>
        <Subtitle content="     " />
        <li className="section-p-light line-gap ">
                       
        </li>
        <li className="section-p-light line-gap ">
                 
        </li>
        <li className="section-p-light line-gap ">
               QA      
        </li>
        <li className="section-p-light line-gap ">
               CS    
        </li>
      </ExpContainer>
    </>
  );
};

export default Web41;

const ExpContainer = styled.div`
  .up {
    z-index: 5;
  }
  .margin {
    height: 4rem;
  }
  .line-gap {
    padding-bottom: 1rem;
  }
  .flex-row {
    display: flex;
    flex-direction: row;
  }

  .title-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    .title-text {
      color: #fff;
      font-size: 3rem;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }
  .info-container {
    display: flex;
    flex-direction: row;
    gap: 5rem;
    justify-content: space-between;
    margin-bottom: -13rem;
    .img {
      width: 17rem;
    }
    .content {
      width: 45rem;
    }
    .info-box {
      display: flex;
      flex-direction: row;
      padding-top: 2rem;
      padding-bottom: 3rem;
      gap: 2rem;
      width: 45rem;
      .info-col {
        display: flex;
        flex-direction: column;
        gap: 1rem;
      }
    }
  }
`;
